//This script creates oscillating lines. It's made with a help of an example from Happy Coding: https://happycoding.io/examples/p5js/arrays/oscillating-lines

let canvasDiv = document.getElementById('canvasContainer');
let divWidth = canvasDiv.offsetWidth;
let divHeight = canvasDiv.offsetHeight;

const lines = 200;
let lineHeights = [];
let lineSpeeds = [];

function setup() {
  createCanvas(divWidth, divHeight).parent("canvasContainer");

  for (let x = 0; x < lines; x++) {
    const lineHeight = random(50, 200);
    lineHeights[x] = lineHeight;
    lineSpeeds[x] = random(-10, 20);
  }
}

function draw() {
  background('#FDE033');

  for (let i = 0; i < lines; i++) {
    lineHeights[i] += lineSpeeds[i];

    if (lineHeights[i] < 50 || lineHeights[i] > height) {
      lineSpeeds[i] *= -1;
    }

    const x = width * (i / lines);
    const lineWidth = divWidth / lines;

    fill(0);
    stroke('#FDE033');
    rect(x, height - lineHeights[i] / 1.5,
      lineWidth, (lineHeights[i]));
  }
}

function windowResized() {
  divWidth = canvasDiv.offsetWidth;
  divHeight = canvasDiv.offsetHeight;
  resizeCanvas(divWidth, divHeight);
}